.img-header {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.header {
  height: 170px;
}
.title {
  color: var(--white);
  font-size: 40px;
  -webkit-transform: translateY(-135%);
      -ms-transform: translateY(-135%);
          transform: translateY(-135%);
  z-index: 9999;
  margin-left: 10%;
}

@media (min-width: 600px) {
  .header {
    height: 260px;
  }
}

@media (min-width: 1000px) {
  .header {
    height: 365px;
  }
}



